@import "./../../constants/variables.scss";

.ProgressBar {
  &.sm-graphics-1 {
    color: $blueGraphics1;
    .CircularProgressbar .CircularProgressbar-path {
      stroke: $blueGraphics1;
    }
    strong {
      display: none;
    }
  }

  /* &.sm-graphics-1::after {
    border-right:2px solid $grayLines;
    height: 10%;
    position: absolute;
    top:- 10% !important;
  } */

  &.sm-graphics-2 {
    color: $blueGraphics2;
    .CircularProgressbar .CircularProgressbar-path {
      stroke: $blueGraphics2;
    }
  }

  &.sm-graphics-3 {
    color: $blueGraphics3;
    .CircularProgressbar .CircularProgressbar-path {
      stroke: $blueGraphics3;
    }
  }

  &.sm-graphics-4 {
    color: $blueGraphics4;
    .CircularProgressbar .CircularProgressbar-path {
      stroke: $blueGraphics4;
    }
  }

  &.sm-graphics-5 {
    color: $blueGraphics5;
    .CircularProgressbar .CircularProgressbar-path {
      stroke: $blueGraphics5;
    }
  }

  &.sm-graphics-6 {
    color: $blueGraphics6;
    .CircularProgressbar .CircularProgressbar-path {
      stroke: $blueGraphics6;
    }
    strong {
      display: none;
    }
  }

  &.sm-lineBottom {
    position: relative;
  }

  /* &.sm-lineBottom::after {
    content: "";
    border-right: 2px solid $grayLines;
    height: 10%;
    position: absolute;
    top: 100%;
  } */

  &.sm-graphics-1 {
    margin-top: 2em;
  }
  &.sm-graphics-2 {
    margin-bottom: 2em;
  }
  &.sm-graphics-3 {
    margin-bottom: 2em;
  }
  &.sm-graphics-4 {
    margin-bottom: 2em;
  }
  &.sm-graphics-5 {
    margin-bottom: 2em;
  }
  &.sm-graphics-6 {
    margin-bottom: 2em;
  }
  &.sm-graphics-2::before {
    content: "";
    border-top: 2px solid $grayLines;
    width: 60%;
    position: absolute;
    left: 91%;
    top: 50%;
  }

  &.sm-graphics-3::before {
    content: "";
    border-top: 2px solid $grayLines;
    width: 60%;
    position: absolute;
    left: 91%;
    top: 50%;
  }
  &.sm-graphics-4::before {
    content: "";
    border-top: 2px solid $grayLines;
    width: 60%;
    position: absolute;
    left: 91%;
    top: 50%;
  }

  .sm-graphics-6 {
    color: $blueGraphics6;

    .CircularProgressbar .CircularProgressbar-path {
      stroke: $blueGraphics6;
    }
  }

  .sm-iconSize {
    width: 40px;
    padding-bottom: 10px;
  }
  .sm-sizePercentage {
    font-size: 15px;
    margin-top: -5;
  }

  @media (min-width: 1441px) {
    .sm-iconSize {
      width: 110px;
      padding-bottom: 10px;
    }

    .sm-sizePercentage {
      font-size: 20px;
      margin-top: -5;
    }
  }

  @media (min-width: 1025px) and (max-width: 1440px) {
    .sm-iconSize {
      width: 40px;
      padding-bottom: 10px;
    }

    .sm-sizePercentage {
      font-size: 15px;
      margin-top: -5;
    }

    &.sm-graphics-2::before {
      width: 70%;
    }

    &.sm-graphics-3::before {
      width: 70%;
    }
    &.sm-graphics-4::before {
      width: 70%;
    }
  }

  @media (max-width: 1024px) and (min-width: 769px) {
    &.sm-graphics-2::before {
      width: 50%;
    }

    &.sm-graphics-3::before {
      width: 50%;
    }
    &.sm-graphics-4::before {
      width: 50%;
    }
  }

  @media (max-width: 768px) {
    &.sm-graphics-2::before {
      width: 40%;
    }

    &.sm-graphics-3::before {
      width: 40%;
    }
    &.sm-graphics-4::before {
      width: 40%;
    }
    .sm-sizePercentage {
      font-size: 10px;
    }
  }

  @media (max-width: 767px) {
    &.sm-graphics-2::before {
      border: none;
    }

    &.sm-graphics-3::before {
      border: none;
    }
    &.sm-graphics-4::before {
      border: none;
    }
    .sm-sizePercentage {
      font-size: 25px;
    }
  }

  //Clases para cambiar color de logo
  .sm-iconComplete1 {
    width: 25%;
    .cls-1 {
      fill: $blueGraphics1 !important;
    }
  }
  .sm-iconComplete2 {
    width: 25%;
    .cls-1 {
      fill: $blueGraphics2 !important;
    }
  }
  .sm-iconComplete3 {
    width: 25%;
    .cls-1 {
      fill: $blueGraphics3 !important;
    }
  }
  .sm-iconComplete4 {
    width: 25%;
    .cls-1 {
      fill: $blueGraphics4 !important;
    }
  }
  .sm-iconComplete5 {
    width: 25%;
    .cls-1 {
      fill: $blueGraphics5 !important;
    }
  }
  .sm-iconComplete6 {
    width: 25%;
    .cls-1 {
      fill: $blueGraphics6 !important;
    }
  }
  .sm-iconSize {
    width: 25%;
  }
  //Fin clases paara cmabiar color de logo
}
