.Navbar {
}

.img-logo-navbar {
  height: 2em;
  width: auto;
}

.navbar-bg-transparent {
  background-color: transparent !important;
}

.text-color-grey {
  color: #a2a2a1 !important;
}

.button-log-yellow {
  background-color: #facb30;
  color: white !important;
  border-radius: 6em;
}

.separator-right {
  @media screen and (min-width: 992px) {
    &:after {
      display: inline-block;
      content: "|";
      padding: 0 15px;
      font-weight: 700;
    }
  }
}

.navbar-toggler,
.collapsed {
  background-color: #facb30;
}
