@import "../../constants/variables.scss";

.Accordion {
  .styleAccordion {
    margin-top: 1em;
  }

  .styleAccordionButtonGroup {
    width: 100%;
  }

  .styleAccordionButton {
    background-color: $backgroundColorMod;
    border-color: $backgroundColorMod;
    text-align: left;
    padding-left: 1em;
    margin: $marginButtonsDiagnose;
    border-radius: 0;
    z-index: 1061 !important;
  }

  .styleAccordionButton:focus::before {
    content: "";
    border-top:12px solid transparent;
    border-bottom: 12px solid transparent;
    border-left: 23px solid $backgroundColorModHover;
    top: 5px;
    left: 100%;
    position: absolute;
  }

  .styleAccordionButton:hover {
    background-color: $backgroundColorModHover;
  }

  .styleAccordionButton:focus {
    background-color: $backgroundColorModHover;
  }
}

.popover .arrow{
  display:none;
}

.stylePopover {
  max-width: $widthDiagnose;
  top: 10.5% !important;
  width: $widthDiagnose;
  background-color: $backgroundColorSub;
  border-radius: 0;
}

.stylePopoverButtonGroup {
  width: 100%;
}

.stylePopoverButton {
  display: flex;
  justify-content: space-between;
  background-color: $backgroundColorSub;
  border-color: $border-colorSub;
  color: #89898B;
  // text-align: left;
}

.stylePopoverButton:hover {
  background-color: gray;
  border-color: $border-colorSub;
}

.stylePopoverButton:focus {
  background-color: gray;
  border-color: $border-colorSub;
}
