@import "../../constants/variables.scss";

.Companies {

  .styleArrowLink{
    width: $widthButtonCompany;
    padding: 0rem 1rem
  }

  .styleButtonGroup {
    width: 100%;
    margin-top: 2rem;
  }

  .styleNavLink {
    width: 100%;
  }

  .styleButton {
    width: 100%;
    background-color: $backgroundColorMod;
    border-color: $backgroundColorMod;
    text-align: left;
    padding-left: 1em;
    margin: $marginButtonsDiagnose;
    border-radius: 0;
    z-index: 1061 !important;
    text-transform: capitalize;
  }
.styleButton:hover {
    background-color: $backgroundColorModHover;
  }

  .styleButton:focus {
    background-color: $backgroundColorModHover;
  }

  .styleButtonAdd{
    width: 100%;
    background-color: #FACB30;
    border-color: #FACB30;
    color: #4A4A81;
    text-align: left;
    padding-left: 1em;
    margin: $marginButtonsDiagnose;
    border-radius: 0;
  }

  .styleButtonAdd:focus{
    background-color: #dbad14;
    border-color: #dbad14;
    color: #4A4A81;
  }
}
