@import "../../constants/variables.scss";

.GraphicsPage {
  .sm-background {
    background-image: url("./../../img/backgrounds/backgroundsLogin.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    min-height: 100vh;
    width: 100%;
    position: relative;
  }

  .sm-contentProgressBar {
    height: auto;
  }

  .sm-circle {
    margin-top: 15%;
