  }

  .sm-circleTop {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 15%;
  }
  .sm-textHelpButton {
    font-size: 35px;
  }
  .sm-text {
    text-align: justify;
  }

  .sm-titleGraphics {
    font-weight: bold;
    color: $purpleText;
    font-size: 25px;
  }

  .sm-graphics {
    padding: 50px;
  }

